
.adding-form{
    position: absolute;
    display: grid;
    grid-template-rows: 2rem 2rem max-content;
    gap: 10px;
    padding: 10px;
    top: 0px;
    left: 50%;
    transform: translate(-50%);
    background-color: rgba(255, 255, 255, 1);
    border: 2px solid #000;
    color: rgb(0, 0, 0);
    font-weight: bold;
}

.form-drop{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-color: rgba(116, 116, 116, 0.335);
    border: 2px solid #000;
}
.form-item{
    display: grid;
    grid-template-columns: 100px 200px;
}
.image-submit{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.image-submit-image{
    position: relative;
}
.image-submit-image:hover::after{
    position: absolute;
    content: 'ðŸ”„';
    font-size: 3em;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}