.content{
    position: relative;
    display: grid;
    z-index: 0;
    grid-template-rows: 50px 1fr 50px;
    min-height: 100vh;
    background-image: url(../../../public/images/galary/Dnipro.jpg);
    background-size: cover;
    background-position: center;
}
.content::before{
    content: '';
    z-index: -1;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(29, 79, 102, 0.284);
}
header{
    display: flex;
    justify-content: center;
    align-items: center;
}
ol{
    display: grid;
    grid-template-columns: 200px 300px 200px;
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 1.2rem;
}
li{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 3rem;
    margin: 0;
    padding: 0;
}
li a{
    padding: 0.1em;
    font-size: 1.2em;
    max-height: 2em;
    font-weight: bold;
    text-decoration: none;
    color: white;
    transition: 150ms;

}
li a:hover{
    text-decoration: underline;
    font-weight: normal;
    font-size: 1.23em;
}
footer{
    text-align: center;
    padding: 1em;
    color: white;
}