.attractions{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    flex-wrap: wrap;
    color: white;
    margin: 1em;
}
.attractions-item{
    display: grid;
    grid-template-rows: 5rem max-content 1fr;
    grid-template-areas: 
                        't t'
                        'i i'
                        'a a';
    color: black;
    background-color: rgba(255, 255, 255, 0.921);
    padding: 10px;
    font-size: 1.2rem;
}
.attractions-item .item-title{
    grid-area: t;
    text-align: center;
}
.attractions-item .item-about{
    grid-area: a;
}
.attractions-item .item-image{
    grid-area: i;
    width: 100%;
}
.attractions-item .item-image img{
    max-width: 100%;
    max-height: 100%;
}