.galary{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows:repeat(auto-fill, 300px);
    gap: 20px;
    margin: 0 1em;
    position: relative;
}


.add-new-pic{
    background-color: rgba(255, 255, 255, 0.278);
    border: 10px solid rgb(255, 255, 255, 0.678);
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 4em;
    color: white;
    font-weight: bold;
}
.add-new-pic:hover{
    background-color: rgba(255, 255, 255, 0.478);
    border: 10px solid rgb(255, 255, 255);
}
.galary-item img{
    width: 100%;
    height: 100%;
}
.user-pic img{
    width: 100%;
    max-height: 300px;
}